* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}
.example-container {
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
}
.options-panel {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2;
}
.options-panel img {
  border: solid 2px;
  width: 50px;
  height: 50px;
  object-fit: cover;
  cursor: pointer;
}
.options-panel img.selected {
  border-color: green;
}
